* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: F1Regular;
  src: url(https://www.formula1.com/etc/designs/fom-website/fonts/F1Regular/Formula1-Regular.ttf);
}

@font-face {
  font-family: F1Bold;
  src: url(https://www.formula1.com/etc/designs/fom-website/fonts/F1Bold/Formula1-Bold.ttf);
}

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  background: #f8f9fa;
}

select.form-control, textarea.form-control, input.form-control {
  font-size: 16px;
}

input[type=file] {
  width: 100%;
}
.App {
  padding: 30px;
  font-family: 'Titillium Web', sans-serif;
}

.logo {
  font-size: 20px;
  font-family: F1Bold;
  text-align: center;
  margin-bottom: 20px;
}

.chevron {
  display: inline-block;
  font-size: 70px;
  color: #dc3545;
  font-family: F1Bold;
  line-height: 70px;
  height: 70px;
}

.logo .text-container {
  display: inline-block;
  text-align: left;
  padding: 0px 5px;
}

.logo .teammate {
  margin-top: -8px;
  margin-bottom: -8px;
  letter-spacing: 0.4px;
}

h1 {
  font-size: 24px;
  font-family: F1Bold;
  text-align: center;
  margin-bottom: 20px;
}

.inputs {
  text-align: center;
  padding: 20px;
}

.inputs .text {
  display: inline-block;
  padding: 0px 20px;
  font-size: 20px;
  font-weight: bold;
}

.react-autosuggest__container {
  position: relative;
  display: inline-block;
}

@media only screen and (max-width: 1000px) {
  .inputs .text {
    display: block;
  }
  .react-autosuggest__container {
    display: block;
  }
}

.react-autosuggest__input {
  text-align: center;
  background-color: #343a40;
  color: #fff;
  font-family: F1Regular;
  width: 240px;
  height: 30px;
  padding: 20px 20px;
  border: none;
  border-radius: 4px;
}

.react-autosuggest__input--focused {
  outline: none;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  font-family: F1Regular;
  width: 240px;
  border: none;
  border-radius: 4px;
  background-color: #343a40;
  color: #fff;
  z-index: 2;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd;
}

.buttons {
  text-align: center;
  padding: 10px;
}

.btn-default, .btn-default:focus {
  margin: 5px;
  color: #fff;
  font-family: F1Regular;
  font-size: 16px;
  border: none;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  opacity: 0.8;
}

.fas {
  margin-right: 10px;
}

#clear {
  background-color: #dc3545;
}

#switch {
  background-color: #17a2b8;
}

#calculate {
  background-color: #28a745;
}

#clear:hover, #switch:hover, #calculate:hover {
  opacity: 1;
  color: #fff;
}

.result {
  padding: 20px 0px;
  border-radius: 4px;
  min-height: 300px;
  font-family: F1Regular;
  color: #343a40;
  font-size: 14px;
}

.result-title {
  font-size: 30px;
  font-family: 'Titillium Web', sans-serif;
  color: #6c757d;
  margin-top: 5px;
  margin-bottom: 5px;
}

.result-title-fail {
  font-size: 30px;
  font-family: 'Titillium Web', sans-serif;
  color: #6c757d;
  margin-top: 78px;
  margin-bottom: 5px;
}

.result-subtitle {
  margin-bottom: 20px;
  color: #6c757d;
}

.wrapper {
  padding: 4px 0px;
  overflow: hidden;
}

.result-row:last-child .arrow-down {
  display: none;
}

.row-item {
  height: 70px;
  float: left;
  width: calc(50% - 35px);
  box-sizing: border-box;
}

.left {
  text-align: right;
}

.right {
  text-align: left;
}

.middle {
  margin-left: auto;
  margin-right: auto;
  width: 70px;
}

.beat {
  background-color: #343a40;
  color: #fff;
  padding: 10px;
}

.year {
  font-size: 12px;
}

.driver-left {
  background-color: #6c757d;
  color: #fff;
  border-radius: 4px 0px 0px 4px;
  display: inline-block;
  padding: 10px;
}

.driver-right {
  background-color: #6c757d;
  color: #fff;
  border-radius: 0px 4px 4px 0px;
  display: inline-block;
  padding: 10px;
}

.arrow-down {
  width: 0px;
  height: 0px;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 15px solid #dc3545;
  margin: 10px auto;
  margin-top: 0px;
}

.footer {
  color: #000;
  margin-top: 10px;
  font-size: 11px;
}

.form-control:focus, .form-control:active, .btn-default:focus, .btn-default:active {
  outline: none !important;
  box-shadow: none !important;
  -webkit-box-shadow: none !important;
}
